.letterhead-header .header-wrapper {
  display: grid;
  grid-template-areas: "logo icons" "logo main" ". sub";
  grid-template-rows: auto 38px 28px;
  grid-template-columns: 1fr 1fr;
  padding: 70px 60px 30px;
}
.letterhead-header .subheader-wrapper:not(:empty) {
  padding-bottom: 30px;
}
.letterhead-header .logo-wrapper {
  grid-area: logo;
  align-self: flex-end;
  width: 300px;
  height: 100px;
}
.letterhead-header .icon-wrapper {
  display: flex;
  grid-area: icons;
  align-items: center;
  justify-content: flex-end;
}
.letterhead-header .icon-wrapper .icon {
  width: 24px;
  height: 24px;
  margin-left: 10px;
  cursor: pointer;
  opacity: 0.5;
}
.letterhead-header .right {
  color: #2d2d2d;
  font-family: Lato;
  font-style: normal;
  font-stretch: normal;
  letter-spacing: normal;
  text-align: right;
}
.letterhead-header .right.main {
  grid-area: main;
  font-weight: 800;
  font-size: 28px;
  line-height: 1.36;
}
.letterhead-header #right-main-label {
  color: #888e8e;
  font-weight: normal;
}
.letterhead-header .right.sub {
  grid-area: sub;
  font-size: 18px;
  line-height: 1.56;
}