button {
  background-size: 200%;
  background-position: 0;
  border: none;
  border-radius: 12.5px;
  box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.2);
  color: #ffffff;
  font-size: 14px;
  height: 25px;
  padding: 4px 18px;
  text-align: center;
  transition: 0.3s;
}
button:hover {
  background-position: 100%;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3) !important;
}

button:disabled {
  background-color: #cacbcc;
  background-image: none;
  cursor: not-allowed;
}

.green {
  background-image: linear-gradient(to right, #10c0c6, #4dde90, #10c0c6);
}

.gray {
  background-image: linear-gradient(to right, #888e8e, #cacbcc, #888e8e);
}