gor-calendar-header {
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
gor-calendar-header button {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-weight: normal;
}
gor-calendar-header button:hover {
  box-shadow: none !important;
}
gor-calendar-header .mat-calendar-previous-button,
gor-calendar-header .mat-calendar-next-button {
  background: none !important;
  border: none;
  box-shadow: none;
}