.section-title h4 {
  color: #F9F9F9;
  display: flex;
  flex-direction: row;
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 0px;
  margin: 3px 0;
  padding: 0;
  text-align: left;
  text-transform: capitalize;
}
.section-title.inverse-color h4 {
  padding: 0;
  color: #666;
}
.section-title .content-container {
  flex-grow: 2;
}

section-title, .section-title {
  width: 100%;
}