:host {
  box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.13);
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  overflow: hidden;
  background: #fff;
  /* stylelint-disable selector-max-compound-selectors */
  /* stylelint-enable selector-max-compound-selectors */
}
:host gor-section-title {
  display: block;
  height: 25px;
  padding: 2px 8px;
}
:host ::ng-deep gor-section-title .content-container {
  position: relative;
}
:host gor-section-title .card-subtitle {
  position: absolute;
  top: 0;
  right: 0;
  color: #888e8e;
  font-weight: normal;
  font-size: 12px;
  line-height: 1.33;
  text-transform: none;
}

.card-container {
  display: block;
  flex: 1;
  width: 100%;
  padding: 0 8px 8px;
}
.card-container .card-content {
  display: block;
  width: 100%;
  height: 100%;
  padding-bottom: 8px;
}

/* stylelint-disable property-no-vendor-prefix */
/* stylelint-disable property-no-unknown */
.card-top {
  width: 100%;
  height: 5px;
  -webkit-print-color-adjust: exact;
  print-color-adjust: exact;
}

/* stylelint-enable property-no-unknown */
/* stylelint-enable property-no-vendor-prefix */