pre {
  background: #303030;
  border-radius: 2px;
  border-top: 4px solid #00aeef;
  box-shadow: inset 0 0 10px #000;
  color: #f1f1f1;
  counter-reset: line;
  font-family: monospace;
  height: 100%;
  max-height: 400px;
  max-width: 100%;
  min-height: 100px;
  overflow: auto;
  width: 100%;
}

pre span {
  display: block;
  line-height: 1.5rem;
}
pre span:not(:first-child) {
  margin-top: -18px;
}

pre span:before {
  counter-increment: line;
  content: counter(line);
  display: inline-block;
  border-right: 1px solid #ddd;
  padding: 0 0.5em;
  margin-right: 0.5em;
  color: #888;
  width: 15px;
}

