:host {
  display: block;
  background: #fff;
}

.letterhead-footer {
  display: grid;
  grid-template-areas: "ltop mtop rtop" "lbot mbot rbot";
  grid-template-columns: repeat(3, 1fr);
  margin: 120px 60px 0;
  padding-top: 30px;
  padding-bottom: 92px;
  color: #888e8e;
  font-size: 14px;
  line-height: 1.43;
  border-top: solid 1px #979797;
}
.letterhead-footer > * {
  align-self: flex-end;
}
.letterhead-footer .left {
  justify-self: flex-start;
  line-height: 1.45;
}
.letterhead-footer .left.main {
  grid-area: ltop;
  color: #2d2d2d;
  font-weight: 800;
  font-size: 22px;
}
.letterhead-footer .left.sub {
  grid-area: lbot;
  color: #888e8e;
  font-size: 18px;
}
.letterhead-footer .middle {
  justify-self: center;
  text-align: center;
}
.letterhead-footer .middle.main {
  grid-area: mtop;
}
.letterhead-footer .middle.sub {
  grid-area: mbot;
}
.letterhead-footer .gorila-wrapper {
  justify-self: flex-end;
  text-align: right;
}
.letterhead-footer .gorila-wrapper.main {
  grid-area: rtop;
}
.letterhead-footer .gorila-wrapper.sub {
  grid-area: rbot;
  font-size: 18px;
  line-height: 1.56;
}
.letterhead-footer span {
  margin-right: 7px;
  vertical-align: bottom;
}
.letterhead-footer object,
.letterhead-footer img {
  width: 99px;
  height: 32px;
  object-fit: contain;
}